.header {
    box-shadow: 0 2px 7px 0 rgba(0, 0, 0, .4);
    padding: 10px 0;
    margin-bottom: 30px;
    background-color: #fff;
    font-size: 14px;
}
.header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}
.header-nav-link {
    text-decoration: none;
    display: block;
    padding: 5px 15px;
    border-radius: 10px;
}
.header-nav-link.active {
    font-weight: bold;
}
.header-nav-link:hover, .header-nav-link:focus {
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, .4)inset;
    text-decoration: none;
    outline: none;
}
@media only screen and (min-width: 768px) {
    .header {
        font-size: 16px;
    }
    .header-nav-item {
        margin-left: 20px;
    }
}
@keyframes lds-blocks {
    0% {
      background: #85a2b6;
    }
    12.5% {
      background: #85a2b6;
    }
    12.625% {
      background: #dce4eb;
    }
    100% {
      background: #dce4eb;
    }
  }
  @-webkit-keyframes lds-blocks {
    0% {
      background: #85a2b6;
    }
    12.5% {
      background: #85a2b6;
    }
    12.625% {
      background: #dce4eb;
    }
    100% {
      background: #dce4eb;
    }
  }
  .lds-css {
      width: 100%;
      height: 100%;
      background-color: #a00d1e;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
  }
  .lds-blocks {
    position: relative;
    width:100%;
    height:100%;
  }
  .lds-blocks div {
    position: absolute;
    width: 40px;
    height: 40px;
    background: #dce4eb;
    -webkit-animation: lds-blocks 1s linear infinite;
    animation: lds-blocks 1s linear infinite;
  }
  .lds-blocks {
    width: 172px !important;
    height: 172px !important;
    -webkit-transform: translate(-86px, -86px) scale(0.86) translate(86px, 86px);
    transform: translate(-86px, -86px) scale(0.86) translate(86px, 86px);
  }
.home-page-title {
    font-size: 20px;
}
.fa-sort-asc {
    color: green;
}
.fa-sort-desc {
    color: red;
}
.fa-star-o, .fa-star{
    color: #fff200;
}
.currency-table {
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, .5);
    font-size: 14px;
}
.currency-table td{
    vertical-align: middle;
}
.button-cell {
    width: 8%;
}
@media only screen and (min-width: 768px) {
    .currency-table {
        font-size: 16px;
    }
    .home-page-title {
        font-size: 35px;
    }
}
.converter-title {
    font-size: 30px;
}
.converter-text-input {
    width: 48%;
    padding: 0 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    -moz-appearance: textfield;
}
.converter-text-input::-webkit-outer-spin-button,
.converter-text-input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
.error-indicator-frame {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.error-indicator {
    padding: 20px;
    border: 2px dashed red;
    background-color: orange;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, .4);
    font-size: 30px;
    color: red;
}
.error-indicator h1 {
    font-size: inherit;
    display: inline-block;
    margin-left: 10px;
}
.error-code {
    display: block;
    text-align: center;
}
