.header {
    box-shadow: 0 2px 7px 0 rgba(0, 0, 0, .4);
    padding: 10px 0;
    margin-bottom: 30px;
    background-color: #fff;
    font-size: 14px;
}
.header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: flex-end;
}
.header-nav-link {
    text-decoration: none;
    display: block;
    padding: 5px 15px;
    border-radius: 10px;
}
.header-nav-link.active {
    font-weight: bold;
}
.header-nav-link:hover, .header-nav-link:focus {
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, .4)inset;
    text-decoration: none;
    outline: none;
}
@media only screen and (min-width: 768px) {
    .header {
        font-size: 16px;
    }
    .header-nav-item {
        margin-left: 20px;
    }
}